html,body{box-sizing:border-box;margin:0;padding:0}*,*:before,*:after{box-sizing:inherit}body{font-size:16px;font-weight:400;line-height:1;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}img{max-width:100%;height:auto}.visually-hidden:not(:focus):not(:active),input[type="checkbox"].visually-hidden,input[type="radio"].visually-hidden{position:absolute;overflow:hidden;clip:rect(0 0 0 0);width:1px;height:1px;margin:-1px;padding:0;white-space:nowrap;border:0;-webkit-clip-path:inset(100%);clip-path:inset(100%)}

.App-header.ui.header{display:flex;flex-direction:column;justify-content:center;align-items:center}br{display:none}p{margin:0}.App{text-align:center;font-size:calc(.75rem + 1vmin)}.App-List{list-style:none;text-align:left}.App-Code{display:inline-block;color:indigo;background-color:#f3fafe}.App-header.ui.header{background-color:#282c34;margin:0;min-height:100vh;font-size:calc(1.875rem + 1vmin);color:#f5f5f5}

